.filters {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 250px;
  background-color: rgba(218, 112, 214, 0.3);
  margin: 0;
}

.filters__button {
  height: 50px;
  list-style: none;
  display: flex;
  align-items: center;
}

.filters__link {
  text-decoration: none;
  list-style: none;
  text-transform: uppercase;
}
